	module.exports = {"data":{"profile":{"id":"9bdf3e6e-d4f1-5d26-8216-897467865052","content":"<p><div class=\"content-block  text-center\"><p>Steve Pappone, a Boston area native earned a Bachelor of Fine Arts in Music from the University of Massachusetts Boston. He has studied with local greats Ed Sorrentino, Steve Turner, legendary artist Casey Scheuerell (Al DiMeola, Kitaro) and master percussionist Frank Epstein (Boston Symphony Orchestra). Steve has performed in and around Boston w/many local projects over the years including, Half Past Al, Souled Out, Ginger Vadala, WonderKat, Clutch Grabwell &amp; VooDoo Sol.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>Steve spends his days in the Music Industry working for a prominent instrument manufacturer and manages a successful private drum instruction studio out of his home in the evening. He currently maintains a performing schedule with the spirited jazz sensation &#8220;C-Zone Entertainment&#8221; and dynamic multimedia rock retrospective &#8220;RockPhoria&#8221;, (featuring Danny Klein from The J. Geils Band). Look for Steve in and around &#8220;BeanTown&#8221; with various bands &amp; artists.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>Some Drumming Influences:<br />\nSteve Gadd, Vinnie Colaiuta, Dennis Chambers, Peter Erskine, Joe Morello, Buddy Rich, Tony Williams, Bill Bruford, David Giribaldi, Neil Peart &amp; John Bonham. Some Musical Influences: Jaco Pastorius, Stevie Wonder, Joni Mitchell, Frank Zappa, The Beatles, BNH, Incognito, H. Berlioz, P.I. Tchaikovsky &amp; G. Gershwin.<br />\n</p></div></p>\n","title":"Steve Pappone","acf":{"title":"Drummer","video":null,"facebook":"","instagram":"","testimonials":""},"image":{"localFile":{"childImageSharp":{"thumbnail":{"base64":"data:image/jpeg;base64,","aspectRatio":0.967741935483871,"src":"/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-0a50f.jpg","srcSet":"/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-0008a.jpg 110w,\n/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-331c0.jpg 220w,\n/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-0a50f.jpg 300w","sizes":"(max-width: 300px) 100vw, 300px","originalImg":"/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-0a50f.jpg"},"full":{"src":"/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-ec435.jpg","height":310,"width":300,"srcSet":"/static/82e97d644fe78e3be86a7ea15eab9e11-30122b2e4460b872253d9bd69ed27e89-ec435.jpg 1x"}}}},"yoast":{"title":"","canonical":"","metaDescription":"","linkdex":"","metakeywords":"","noIndex":"","noFollow":"","meta_robots_adv":"","metaKeywords":"","redirect":"","ogTitle":"","ogDescription":"","ogImage":"","twitterTitle":"","twitterDescription":"","twitterImage":""}},"site":{"id":"Site","siteMeta":{"title":"C-Zone Entertainment","subtitle":"Premiere DJs & Musicians | Servicing Greater Boston & New England | 617.320.0723"}}},"pathContext":{"id":"9bdf3e6e-d4f1-5d26-8216-897467865052"}}

